/* Custom properties

	01.Colors
	02.Font family
	03.Font sizes
	04.Font weights
	05.Spacings
	06.Border radius
	07.Transitions
	08.Z-indexes

*/
:root {
  /* Colors */
  --color-neutral-50: hsl(0, 0%, 98%);
  --color-neutral-100: hsl(0, 0%, 90%);
  --color-neutral-200: hsl(0, 0%, 80%);
  --color-neutral-300: hsl(0, 0%, 70%);
  --color-neutral-400: hsl(0, 0%, 60%);
  --color-neutral-500: hsl(0, 0%, 50%);
  --color-neutral-600: hsl(0, 0%, 40%);
  --color-neutral-700: hsl(0, 0%, 30%);
  --color-neutral-800: hsl(0, 0%, 20%);
  --color-neutral-900: hsl(0, 0%, 10%);
  --color-white: hsl(0, 0%, 100%);
  --color-black: hsl(0, 0%, 0%);
  --color-gold: hsl(37, 100%, 61%);
  --color-transparent: transparent;
  /* Font family */
  --font-family-default: Montserrat, Arial, Helvetica, sans-serif;
  /* Font sizes */
  --font-size-xs: 0.75rem;
  --font-size-sm: 0.875rem;
  --font-size-base: 1rem;
  --font-size-lg: 1.125rem;
  --font-size-xl: 2rem;
  /* Font weights */
  --font-weight-thin: 100;
  --font-weight-extralight: 200;
  --font-weight-light: 300;
  --font-weight-base: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-extrabold: 800;
  --font-weight-black: 900;
  /* Spacings */
  --space-0: 0;
  --space-1: 0.25rem;
  --space-2: 0.5rem;
  --space-3: 0.75rem;
  --space-4: 1rem;
  --space-5: 1.25rem;
  --space-6: 1.5rem;
  --space-8: 2rem;
  --space-9: 2.5rem;
  --space-10: 3rem;
  --space-11: 4rem;
  --space-12: 5rem;
  /* Border radius */
  --radius-none: 0;
  --radius-xs: 0.125rem;
  --radius-sm: 0.25rem;
  --radius-base: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;
  --radius-xl: 1rem;
  --radius-2xl: 1.5rem;
  --radius-3xl: 2rem;
  --radius-full: 50%;
  --radius-pill: 9999px;
  /* Transitions */
  --transition-ease-100: 0.2s ease;
  --transition-ease-200: 0.4s ease;
  --transition-ease-300: 0.6s ease;
  --transition-bounce: 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  --transition-elastic: 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  --transition-smooth: 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  /* Z-indexes */
  --z-hide: -1;
  --z-auto: auto;
  --z-base: 0;
  --z-docked: 10;
  --z-dropdown: 1000;
  --z-sticky: 1100;
  --z-banner: 1200;
  --z-overlay: 1300;
  --z-modal: 1400;
  --z-popover: 1500;
  --z-skipLink: 1600;
  --z-toast: 1700;
  --z-tooltip: 1800;
}

/* Base styles

	01.Box model reset
	02.Margin/padding resets
	03.Document level styles
	04.Typography
	05.Links
	06.Lists
	07.Form elements
	08.Media elements
	09.Accessibility

*/
/* Box model reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Margin/padding resets */
:where(h1, h2, h3, h4, p, ul, ol, figure, blockquote, dl, dd) {
  margin-block-end: 0;
}

:where(h1, h2, h3, h4, h5, h6, p, ul, ol, dl):where([class]) {
  margin-block: 0;
}

:where(ul, ol):where([class]) {
  padding-left: 0;
}

:where(body, blockquote, figure, fieldset):where([class]) {
  margin: 0;
}

/* Document level styles */
html,
body {
  height: 100%;
  scrollbar-gutter: stable;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  background-color: var(--color-white);
  color: var(--color-black);
  font-family: var(--font-family-default);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-base);
  line-height: 1.5;
}

/* Typography */
h1,
h2,
h3,
h4,
button,
input,
label {
  line-height: 1.1;
}

h1,
h2,
h3,
h4 {
  text-wrap: balance;
}

/* Links */
a:not([class]) {
  text-decoration-skip-ink: auto;
  color: currentColor;
}

/* Lists */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Form elements */
input,
button,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
}

/* Media elements */
img,
video,
picture {
  display: block;
  max-width: 100%;
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}
/* Layouts */
/* Wrapper styles */
.page-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.page-wrapper > header {
  flex: 0 0 auto;
}
.page-wrapper > header + .offset-header {
  margin-top: 6.5rem;
}
@media (width < 64rem) {
  .page-wrapper > header + .offset-header {
    margin-top: 5.25rem;
  }
}
.page-wrapper > main {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
}
.page-wrapper > footer {
  flex: 0 0 auto;
}

/* Header styles */
.page-header {
  --header-h: 6.25rem;
  position: fixed;
  top: 0;
  z-index: var(--z-sticky);
  padding-block: 2.5rem;
  width: 100%;
  height: var(--header-h);
}
@media (width < 64rem) {
  .page-header {
    --header-h: 5.25rem;
    padding-block: 1.875rem;
  }
}
.page-header__container {
  display: flex;
  align-items: center;
  column-gap: 1.25rem;
}
.page-header__logo {
  position: relative;
  z-index: var(--z-banner);
  display: inline-flex;
  flex: 0 0 auto;
}
.page-header__logo-image {
  width: 100%;
  height: 1.25rem;
  object-fit: contain;
  object-position: center;
}
.page-header__menu {
  display: flex;
  flex: 0 0 auto;
}
@media (width >= 64rem) {
  .page-header__menu {
    position: relative;
  }
}
@media (width < 64rem) {
  .page-header__menu {
    position: fixed;
    inset: 0;
    z-index: var(--z-dropdown);
    flex-direction: column;
    overflow: hidden auto;
    background-color: var(--color-white);
  }
  .page-header__menu::before {
    content: "";
    position: fixed;
    z-index: 1;
    width: 100%;
    height: var(--header-h);
    background: linear-gradient(to bottom, var(--color-white) 75%, transparent);
  }
  .page-header__menu:not(.is-active) {
    display: none;
  }
}
.page-header__menu-list {
  display: flex;
  column-gap: 1rem;
  list-style: none;
}
@media (width >= 64rem) {
  .page-header__menu-list {
    position: relative;
  }
}
@media (width < 64rem) {
  .page-header__menu-list {
    flex-direction: column;
    row-gap: 1rem;
    padding-top: var(--header-h);
    padding-bottom: 1.875rem;
    padding-inline: var(--container-offset, 0.625rem);
  }
}
.page-header__menu-item {
  display: flex;
  flex-direction: column;
  line-height: 1.2;
}
@media (width >= 64rem) {
  .page-header__menu-item {
    position: relative;
  }
}
.page-header__menu-link {
  display: inline-flex;
  color: currentColor;
  font-weight: 600;
  text-transform: uppercase;
}
@media (width >= 64rem) {
  .page-header__menu-link {
    position: relative;
    z-index: 1;
  }
}
@media (width < 80rem) {
  .page-header__menu-link {
    font-size: var(--font-size-sm);
  }
}
.page-header__submenu {
  display: flex;
}
@media (width >= 64rem) {
  .page-header__submenu {
    position: absolute;
    top: calc(50% + var(--header-h) / 2);
    column-gap: 1.25rem;
    width: max-content;
    opacity: 0;
    visibility: hidden;
    transition: all var(--transition-ease-100) 0s;
  }
  .page-header__submenu::before {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: min(100vh, 20rem);
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -2px rgba(0, 0, 0, 0.1);
    background: var(--color-white);
    opacity: 0.875;
    content: "";
  }
}
@media (width < 64rem) {
  .page-header__submenu {
    flex-direction: column;
  }
}
@media (width >= 64rem) {
  .page-header__menu-item:hover > .page-header__menu-link {
    text-underline-offset: 0.25rem;
    text-decoration: underline;
  }
  .page-header__menu-item:hover > .page-header__submenu {
    opacity: 1;
    visibility: visible;
  }
}
.page-header__submenu-list {
  position: relative;
  list-style: none;
}
.page-header__submenu-item {
  margin-top: 0.75rem;
}
@media (width >= 64rem) {
  .page-header__submenu-link {
    font-size: var(--font-size-sm);
  }
}
.page-header__btns {
  display: flex;
  gap: 1rem;
  margin-inline-start: auto;
}
@media (width < 64rem) {
  .page-header__btns {
    position: relative;
    z-index: var(--z-banner);
  }
}

/* Navbar styles */
.page-navbar {
  padding-block: 1.875rem;
  background-color: var(--color-neutral-100);
}
@media (width < 64rem) {
  .page-navbar {
    padding-block: 1.25rem;
  }
}
@media (width < 48rem) {
  .page-navbar {
    padding-block: 0.625rem;
  }
}
.page-navbar__container {
  display: flex;
  justify-content: space-between;
  column-gap: 1rem;
}
@media (width < 64rem) {
  .page-navbar__container {
    flex-direction: column;
    row-gap: 0.25rem;
  }
}
.page-navbar__heading {
  line-height: 1.2rem;
}
.page-navbar__breadcrumbs {
  display: flex;
  align-items: center;
  font-size: var(--font-size-sm);
}
.page-navbar__breadcrumbs > * + * {
  margin-inline-start: 0.5em;
}
.page-navbar__breadcrumbs-link {
  display: inline-flex;
}
.page-navbar__breadcrumbs-link > a {
  color: currentColor;
  text-decoration: none;
}
@media (hover: hover) {
  .page-navbar__breadcrumbs-link > a:hover {
    text-decoration: underline;
    text-underline-offset: 0.25em;
  }
}
.page-navbar__breadcrumbs-link::after {
  content: "→";
  margin-inline-start: 0.5em;
  pointer-events: none;
}
.page-navbar__breadcrumbs-text {
  font-weight: 600;
}

/* Footer styles */
.page-footer {
  padding-block: 2.5rem;
}
.page-footer__container {
  display: flex;
  flex-direction: column;
  row-gap: 6.25rem;
}
@media (width < 48rem) {
  .page-footer__container {
    row-gap: 3.125rem;
  }
}
.page-footer__body {
  display: flex;
  flex-wrap: wrap;
  gap: 1.875rem;
}
@media (width < 48rem) {
  .page-footer__body {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
.page-footer__menu {
  display: flex;
  flex-direction: column;
  font-size: var(--font-size-sm);
}
@media (width >= 48rem) {
  .page-footer__menu--ms-auto {
    margin-inline-start: auto;
  }
  .page-footer__menu--me-auto {
    margin-inline-end: auto;
  }
}
@media (width < 48rem) {
  .page-footer__menu {
    align-items: center;
  }
}
.page-footer__menu-header {
  margin-block-start: 0;
  color: currentColor;
  font-weight: 600;
}
.page-footer__menu-list {
  display: flex;
  flex-direction: column;
  list-style: none;
}
.page-footer__menu-item {
  margin-block-start: 0.25rem;
}
.page-footer__menu-link {
  color: currentColor;
  text-decoration: none;
}
@media (hover: hover) {
  .page-footer__menu-link:hover {
    text-decoration: underline;
    text-underline-offset: 0.25em;
  }
}
.page-footer__foot {
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-footer__social {
  display: flex;
  column-gap: 1rem;
}
.page-footer__social-link {
  color: inherit;
  text-decoration: none;
}
.page-footer__social-icon {
  width: 1.5rem;
  height: 1.5rem;
  fill: currentColor;
}

/* Home slider styles */
.home-slider {
  display: flex;
  flex-direction: column;
}
.home-slider__slide {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  height: 100dvh;
}
.home-slider__slide-image {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.home-slider__slide-body {
  position: relative;
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home-slider__slide-header-image {
  width: min(43.75rem, 90%);
}
.home-slider__slide-foot {
  position: relative;
  flex: 0 0 auto;
  align-items: center;
  margin-block-start: auto;
  padding-block: 1.25rem;
  text-align: center;
}

/* Catalog filters styles */
.catalog-filters {
  padding-block: 1.875rem;
}
@media (width < 48rem) {
  .catalog-filters {
    padding-block: 1.25rem;
  }
}
.catalog-filters__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
}
@media (width < 48rem) {
  .catalog-filters__container {
    flex-direction: column;
    row-gap: 1rem;
  }
}
.catalog-filters__list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem 1rem;
  list-style: none;
}
@media (width < 48rem) {
  .catalog-filters__list {
    justify-content: center;
    text-align: center;
  }
}
.catalog-filters__item {
  display: inline-flex;
  align-items: 1;
}
.catalog-filters__link {
  font-size: var(--font-size-sm);
}
.catalog-filters__link:is(.is-active) {
  font-weight: 600;
  text-decoration: underline;
  text-underline-offset: 0.25rem;
  pointer-events: none;
}
.catalog-filters__button {
  display: flex;
  align-items: center;
  column-gap: 0.5rem;
  padding: 0;
  border: none;
