/* Catalog previews styles */
.catalog-previews {
  margin-bottom: 6.25rem;
  background-color: var(--color-neutral-50);
}
@media (width < 80rem) {
  .catalog-previews {
    margin-bottom: 3.75rem;
  }
}
@media (width < 64rem) {
  .catalog-previews {
    margin-bottom: 1.875rem;
  }
}
@media (width < 48rem) {
  .catalog-previews {
    margin-bottom: 0.625rem;
  }
}
.catalog-previews__container {
  padding-block: 6.25rem;
}
@media (width < 80rem) {
  .catalog-previews__container {
    padding-block: 3.75rem;
  }
}
@media (width < 64rem) {
  .catalog-previews__container {
    padding-block: 1.875rem;
  }
}
@media (width < 48rem) {
  .catalog-previews__container {
    padding-block: 0.625rem;
  }
}
.catalog-previews__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.875rem;
