  width: min(100%, 70rem);
}
@media (width < 64rem) {
  .catalog-previews__grid {
    gap: 1.25rem;
  }
}
@media (width < 48rem) {
  .catalog-previews__grid {
    grid-template-columns: 1fr;
    gap: 0.625rem;
  }
}

/* Section styles */
/* Components */
/* Icon styles */
.icon {
  width: 1.25rem;
  height: 1.25rem;
  fill: currentColor;
}

/* Link styles */
.link:where(a) {
  display: inline-flex;
  color: currentColor;
  text-decoration: none;
}
@media (hover: hover) {
  .link:where(a):hover {
    text-decoration: underline;
    text-underline-offset: 0.25em;
  }
}

/* Icon link styles */
.icon-link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.5rem;
  height: 1.5rem;
  outline: 1px solid transparent;
  outline-offset: 0.25rem;
  border-radius: 50%;
  color: var(--color-black);
  transition: all var(--transition-ease-100) 0s;
}
.icon-link:focus {
  outline-color: currentColor;
  outline-width: 2px;
}
@media (hover: hover) {
  .icon-link:hover {
    outline-color: currentColor;
  }
}
.icon-link__badge {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1/1;
  width: 1rem;
  border-radius: 50%;
  background-color: var(--color-black);
  color: var(--color-white);
  font-size: 0.625rem;
  line-height: 0.75;
  transform: translate(25%, -25%);
}
.icon-link__image {
  color: currentColor;
}

/* Burger styles */
/* Button styles */
.button {
  display: inline-flex;
  column-gap: 0.625rem;
  padding: 1.25rem;
  border: 1px solid var(--color-black);
  background-color: var(--color-black);
  color: var(--color-white);
  font-size: var(--font-size-sm);
  line-height: 1.25rem;
  transition: all var(--transition-ease-100) 0s;
  cursor: pointer;
}
@media (hover: hover) {
  .button:hover {
    border-color: var(--color-neutral-700);
    background-color: var(--color-neutral-700);
  }
}
.button:active {
  border-color: var(--color-neutral-600);
  background-color: var(--color-neutral-600);
}
@media (width < 64rem) {
  .button {
    padding: 0.875rem;
  }
}
.button--outline {
  border-color: var(--color-neutral-700);
  background-color: transparent;
  color: var(--color-neutral-700);
}
@media (hover: hover) {
  .button--outline:hover {
    background-color: var(--color-neutral-700);
    color: var(--color-white);
  }
}
.button--outline:active {
  border-color: var(--color-neutral-600);
  background-color: var(--color-neutral-600);
}

/* Input checkbox styles */
.input-checkbox {
  position: relative;
  display: flex;
  column-gap: 0.5rem;
}
.input-checkbox__input {
  position: absolute;
  top: 0;
  left: 0;
  visibility: hidden;
}
.input-checkbox__icon {
  width: 1.25rem;
  height: 1.25rem;
  background-color: var(--color-neutral-300);
  fill: var(--color-neutral-300);
  cursor: pointer;
}
.input-checkbox__input:checked + .input-checkbox__icon {
  fill: var(--color-white);
}
.input-checkbox__text {
  margin-block-start: 0.2rem;
  color: var(--color-neutral-500);
  font-size: var(--font-size-sm);
}

/* Input counter styles */
.input-counter {
  display: inline-flex;
  border: 1px solid var(--color-neutral-700);
}
.input-counter__button {
  display: flex;
  flex: 0 0 auto;
  padding: 1.25rem;
  border: none;
  background-color: transparent;
  transition: all var(--transition-ease-100) 0s;
  cursor: pointer;
}
@media (hover: hover) {
  .input-counter__button:hover {
    background-color: var(--color-neutral-700);
    color: var(--color-white);
  }
}
.input-counter__button:active {
  border-color: var(--color-neutral-600);
  background-color: var(--color-neutral-600);
}
@media (width < 64rem) {
  .input-counter__button {
    padding: 0.875rem;
  }
}
.input-counter__button-text {
  width: 1.25rem;
  font-size: 1.25rem;
  line-height: 1em;
  text-align: center;
}
.input-counter__input {
  display: flex;
  flex: 1 1 auto;
  justify-content: center;
  padding: 0;
  width: 3.75rem;
  outline: none;
  border: none;
  font-size: var(--font-size-sm);
  line-height: 1.25rem;
  text-align: center;
}

/* Card product styles */
.card-product {
  display: flex;
  flex-direction: column;
  row-gap: 0.625rem;
}
.card-product__head {
  position: relative;
  aspect-ratio: 4/5;
  overflow: hidden;
}
.card-product__head > a {
  width: 100%;
  height: 100%;
  text-decoration: none;
}
.card-product__head img,
.card-product__head video {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.card-product__button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.625rem;
  border: 2px solid var(--color-white);
  background: none;
}
.card-product__button > svg {
  width: 2.5rem;
  height: 2.5rem;
  fill: solid var(--color-white);
}
.card-product__body {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  gap: 0.5rem 1rem;
  font-size: var(--font-size-sm);
}
.card-product__link {
  flex: 1 1 auto;
}
.card-product__price {
  flex: 0 0 auto;
}

